.toggle {
  border: .6px solid #fd5103;
  position: relative;
}

.toggle:after {
  background-color: #fd5103;
}

.toggle:before {
  background-color: #fcfcfc;
}

.toggle:checked {
  background-color: #fd5103;
  border: .6px solid #fcfcfc;
}

.form-toggle {
  align-items: center;
  column-gap: 12px;
  display: flex;
}

.label-toggle, .label-toggle {
  font-family: Poppins, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

.toggle {
  cursor: pointer;
  width: 40px;
  height: 20px;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  -webkit-tap-highlight-color: transparent;
  border-radius: 50px;
  overflow: hidden;
}

.toggle:before, .toggle:after {
  content: "";
  width: 16px;
  height: 16px;
  transition: transform var(--dur);
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 1.3px;
  left: 1px;
}

.toggle:checked:before, .toggle:after {
  transition-delay: calc(var(--dur) * .7);
  transition-timing-function: cubic-bezier(.3, 1.6, .5, .7);
}

.toggle:before {
  transform: translateX(-20px);
}

.toggle:checked:before {
  transform: translateX(20px);
}

.toggle:checked:after {
  transform: translateX(3em);
}

